	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>A</strong></p>\n<p>Today I felt sick since the morning, I was unwilling to have half don and the day of school all together, but my parents came to pick me up.</p>\n<p>I got lunch after going home, but I felt sick and I could hardly put my hands on. In the afternoon, my father and brother went to buy and my mother made okonomiyaki for snacks. I could speak a little about this.</p>\n<p>After resting for a while for a while, I visited the hospital, but when I had nothing to do with it, I got only an intestinal medicine and I felt relieved almost at the evening.</p>\n<p>The dinner was delicious.</p>","frontmatter":{"cover":null,"date":"1990-11-10T21:00:00+09:00","strDate":"1990/11/10","title":"diary"}}},"pathContext":{"path":"/en/blog/1990/11/10-diary/","langKey":"en"}}

